.el-main {
  display: block;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  -ms-flex-preferred-size: auto;
  flex-basis: auto;
  overflow: auto;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 20px;
}

.is-text-danger {
  color: #F56C6C;
}

.box-card.is-hoverable:hover {
  transform: translateY(-3px);
  box-shadow: 0 7px 28px rgba(0, 0, 0, 0.28);
  border-width: 0;
}

.title {
  font-size: 20px;
}

.is-padding-less {
  padding: 0;
}

.is-margin-less {
  margin: 0
}



